import { animated, useSpring } from "react-spring"
import React from 'react';
import scroll from 'assets/img/ui/scroll.png';

import './ScrollIndicator.scss';

export const ScrollIndicator = () => {
    const animationStyles = useSpring({
        loop: { reverse: true },
        from: { bottom: "60px" },
        to: { bottom: "40px" },
        config: {
            duration: 500,
            easing: t => t < .5 ? 2 * t * t : -1 + (4 - 2 * t) * t
        }
    })

    return (
        <div style={animationStyles} className="scroll-indicator-container">
            <animated.img alt="" className="scroll-indicator" src={scroll} style={animationStyles} width={50} height={50} />
        </div>
    )
}